* {
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;
  font-size: 1em;
}

body {
  color: white;
  background-color: black;
}

h2 {
  font-size: 20px;
  -webkit-text-fill-color: black;
  -webkit-text-stroke-width: 0.7px;
  -webkit-text-stroke-color: white;
}

/* "What makes ..." title on Landing(Gateway) Page*/

.choose-gateway-word-title {
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 3.5em;
  padding-left: 0.5em;
  -webkit-text-fill-color: black;
  /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  padding-top: 3em;
  background-image: url("../images/Calgary_DT.jpg");
  padding-bottom: 75%;
  margin: auto;
}

/*Dropdown list on Landing (Gateway) Page*/

.list-spacer {
  margin-top: 20px;
  text-align: left;
  background-color: transparent !important;
}

.list-style {
  color: rgb(0, 0, 0);
  font-size: 1em;
  background-color: transparent !important;
}

/*Let's Go Button on Landing(Gateway) Page*/

.button-spacer {
  padding-left: 235px;
}

.button-styling {
  text-decoration: none;
  text-align: center;
  -webkit-text-fill-color: black;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  font-weight: 800;
}

.button-lets-go {
  background-color: rgb(10, 10, 10);
  font-size: 20px;
}

/* Navbar */

.navbar-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 6vh;
  bottom: 0px;
  position: fixed;
  padding-top: 1%;
  background-color: black;
  z-index: 10;
}

.nav-links {
  color: rgb(182, 174, 174);
  font-size: 1.5rem;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

/*  */

.category-explore {
  text-align: center;
  font-size: 2.5em;
  height: 100vh;
}

/* Classification Page */

.container-flex {
  display: flex;
  flex-direction: column;
}

/* Project Title on Classification Page */

.projects-title {
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5em;
}

.project-title-spacer {
  padding-bottom: 7em;
}

/* Projects Slider on Classification, Map and Individual Project Page */

.slider-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.slider-container-left {
  display: flex;
}

.slider-container-right {
  display: flex;
}

.slide {
  opacity: 0;
}

.slide.active {
  opacity: 1;
  transition-duration: 1.5s;
}

/* Project Slider Buttons and Border */

.btn-large {
  background-color: black;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  border: 1.5px solid white;
  height: 25vh;
  width: 65vw;
  box-shadow: inset 0 0 1000em 40px rgba(0, 0, 0, 1);
}

/* Slider text */

.btn-text {
  font-size: 20px;
  text-decoration: none;
  color: black;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 0.7px;
  -webkit-text-stroke-color: black;
  height: 25vh;
  width: 65vw;
}

/* Slider: Project Name Title */

.project-name-align {
  display: flex;
  text-transform: uppercase;
  font-weight: 800;
  letter-spacing: 0.5px;
}

/* Classification Page: Feature Words on Slider */

.count {
  bottom: 0px;
  font-size: 18px;
}

.arrow {
  font-size: 2rem;
  z-index: 10;
  user-select: none;
}

/* Project Page */

.container-grid {
  display: grid;
  padding-bottom: 15%;
}

.border-top {
  border-top: 2px solid gray;
}

.border-bottom {
  border-bottom: 2px solid gray;
}

.border-right {
  border-right: 2px solid gray;
}

.container-grid-two-in-the-row {
  display: grid;
  grid-template-columns: repeat(2, auto);
}

.project-header {
  overflow: hidden;
  display: flex;
}

.navbar-project-page {
  position: unset;
}

.project-padding {
  padding: 1em 1em 1em 1em;
}

.project-padding-left {
  padding: 0em 1em 0em 1em;
}

.project-margins {
  margin: 1em 1em 1em 1em;
}

.project-title {
  font-size: 9.5vw;
  padding: 0.5em 2em 0em 0.4em;
  position: absolute;
  font-weight: 800;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 1.25px;
  -webkit-text-stroke-color: black;
  line-height: 28px;
}

.project-description {
  font-size: 1.1em;
}

.classificationName {
  text-transform: uppercase;
}

.descriptionFormat {
  text-indent: 1.5em;
  justify-content: left;
}

/* Project Buttons under Header */

.project-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 6vh;
  bottom: 0px;
}

.slider-on-project-page {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 28vh;
  bottom: 6.2vh;
  position: static;
  padding-top: 1%;
  background-color: black;
}

/* Classification Page */

/* Header "What makes..." on Classification Page and Map Page */

.header-container {
  background-color: black;
  position: fixed;
  text-align: center;
  font-size: 5em;
  top: 0;
  width: 100%;
  height: 100px;
}

/* Classification Title Text on Classification Page */

.title-text {
  color: white;
  font-size: 1.75rem;
  /*  */
  -webkit-text-fill-color: black;
  /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 0.75px;
  -webkit-text-stroke-color: white;
  /*  */
  text-decoration: none;
  padding: 1rem 1rem 1rem 2rem;
}

.title-text-padding {
  padding-left: 1.5em;
}

/* Classification Container on Classification Page */

.classification-container {
  background-color: black;
  text-align: left;
  top: 120px;
  width: 100%;
  padding-top: 110px;
}

/* Classification Container Description Text on Classification Page */

.description-text {
  color: white;
  font-size: 1 rem;
  text-decoration: none;
  padding: 0rem 1rem 1rem 2rem;
}
